(function () {
    'use strict';
  
    angular.module('RockDroid.pages.dashboard')
        .controller('DashboardCtrl', DashboardCtrl);
  
    /** @ngInject */
    function DashboardCtrl($scope, $timeout, baConfig, baUtil) {
        var pieColor = baUtil.hexToRGB(baConfig.colors.defaultText, 0.2);
        $scope.charts = [{
            color: pieColor,
            description: 'Projetos criados',
            stats: '5',
            icon: 'person',
          }, {
            color: pieColor,
            description: 'Etapas criadas',
            stats: '11',
            icon: 'money',
          }, {
            color: pieColor,
            description: 'Afloramentos criados',
            stats: '15',
            icon: 'face',
          }, {
            color: pieColor,
            description: 'Número de alunos',
            stats: '15',
            icon: 'refresh',
          }
          ];

          function loadPieCharts() {
            $('.chart').each(function () {
              var chart = $(this);
              chart.easyPieChart({
                easing: 'easeOutBounce',
                onStep: function (from, to, percent) {
                  $(this.el).find('.percent').text(Math.round(percent));
                },
                barColor: chart.attr('rel'),
                trackColor: 'rgba(0,0,0,0)',
                size: 84,
                scaleLength: 0,
                animation: 2000,
                lineWidth: 9,
                lineCap: 'round',
              });
            });
      
            $('.refresh-data').on('click', function () {
              updatePieCharts();
            });
          }
      
          function updatePieCharts() {
            $('.pie-charts .chart').each(function(index, chart) {
              $(chart).data('easyPieChart').update(getRandomArbitrary(55, 90));
            });
          }
      
          $timeout(function () {
            loadPieCharts();
            updatePieCharts();
          }, 1000);
  
          
          function getRandomArbitrary(min, max) {
            return Math.random() * (max - min) + min;
          }
    }
  })();
  
